.dialogs {
    display: grid;
    grid-template-columns: 3fr 9fr;
    margin: 10px 8% 20px 0;
}

.dialogs_items{
    min-height: 100vh;
    border-right: 2px solid #7c7b96;
}

.dialog {
    margin:10px 0;
}

.dialog .ava{
    color: #ff8234;
    text-align: center;
    margin-right: 7px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #565656;
    float: left;
}

.dialog a{
    padding: 10px;
    text-decoration: none;
    color: #222222;
    width: 90%;
    background-color: #c9eff6;
    border-radius: 10px;
    cursor: pointer;
    transition: 0.25s;
    display: block;
    line-height: 2.3rem;
    font-size: 16px;
}

.dialog a:hover{
    background-color: #5cf287;
}

.dialog a.active {
    background-color: #5cf287;
}

.messages{
    margin: 10px 30px 20px;
}

.message{
    float: left;
    padding: 15px;
    background-color: #e2e3ff;
    margin: 10px 0;
    width: 55%;
    border-radius: 10px;
    border: 1px solid #dbdbdb;
}

.messageMe{
    float: right;
    padding: 15px;
    background-color: #e2e3ff;
    margin: 10px 0;
    width: 55%;
    border-radius: 10px;
    border: 1px solid #dbdbdb;
}

.newMessage{

}

.newMessage textarea {
    margin: 20px auto 10px;
    width: 100%;
    height: 100px;
    padding: 12px 20px;
    box-sizing: border-box;
    border: 2px solid #ccc;
    border-radius: 10px;
    background-color: #eefaff;
    font-family: Roboto,sans-serif;
}

.newMessage button {
    background-color: #4CAF50;
    border: none;
    color: white;
    padding: 10px 25px;
    text-decoration: none;
    margin: 5px;
    cursor: pointer;
    font-weight: 700;
    transition: 0.25s;
}

.newMessage button:hover{
    background-color: #3ddf44;
    color: white;
}

.newMessage button:active{
    background-color: #4CAF50;
}
